import { Box, Typography } from "@mui/material";
import "./filmAboutPage.css";

export const MovieAboutDetails = ({ movieAboutDetails, movieAboutCredits }) => {
  const directingPerson = movieAboutCredits.crew.find(
    (person) => person.known_for_department === "Directing"
  );

  return (
    <Box className="movieDetailsContainer">
      <Typography className="movieDetailsItem">
        <strong>:</strong>{" "}
        {movieAboutDetails.production_countries[0].name}
      </Typography>
      <Typography className="movieDetailsItem">
        <strong>:</strong> {movieAboutDetails.release_date.split("-")[0]}
      </Typography>
      <Typography className="movieDetailsItem">
        <strong>:</strong> {movieAboutDetails.genres[0].name}
      </Typography>
      <Typography className="movieDetailsItem">
        <strong>:</strong> {directingPerson ? directingPerson.name : '  '}
      </Typography>
      <Typography className="movieDetailsItem">
        <strong>:</strong> {movieAboutDetails.overview}
      </Typography>
      <Typography className="movieDetailsItem">
        <strong>:</strong> {movieAboutDetails.budget + "$"}
      </Typography>
      <Typography className="movieDetailsItem">
        <strong>:</strong> {movieAboutDetails.vote_count}
      </Typography>
      <Typography className="movieDetailsItem">
        <strong>:</strong> {movieAboutDetails.runtime + " minutes"}
      </Typography>
    </Box>
  );
};
